export default function MyFriends({ myFriends, theme }) {
  const cardStyle = {
    marginTop: "15px",
    padding: "10px",
    background: theme === "dark" ? "#2a2a2a" : "#eee",
    borderRadius: "6px",
    display: "flex",
    alignItems: "center",
  };

  if (!myFriends || myFriends.length === 0) return <p>You have no friends yet</p>;

  return (
    <div style={{ marginTop: "20px" }}>
      <h2>My Friends</h2>
      {myFriends.map(f => (
        <div key={f.id} style={cardStyle}>
          <span>{f.friend.name || f.friend.email}</span>
        </div>
      ))}
    </div>
  );
}
