"use client";

import { useContext, useEffect, useState } from "react";
import { AuthContext } from "../../context/AuthContext";
import { ThemeContext } from "../../context/ThemeContext";
import { useRouter } from "next/navigation";

// Components
import Sidebar from "./components/Sidebar";
import UserInfo from "./components/UserInfo";
import CreatePostForm from "./components/CreatePostForm";
import FriendSuggestions from "./components/FriendSuggestions";
import FriendRequests from "./components/FriendRequests";
import MyFriends from "./components/MyFriends";

export default function ProfilePage() {
  const { user } = useContext(AuthContext);
  const { theme } = useContext(ThemeContext);
  const router = useRouter();

  const [showForm, setShowForm] = useState(false);
  const [showSuggestions, setShowSuggestions] = useState(false);
  const [showFriendRequests, setShowFriendRequests] = useState(false);
  const [showMyFriends, setShowMyFriends] = useState(false);

  const [suggestedUsers, setSuggestedUsers] = useState([]);
  const [friendRequests, setFriendRequests] = useState([]);
  const [myFriends, setMyFriends] = useState([]);

  const [title, setTitle] = useState("");
  const [content, setContent] = useState("");
  const [imageFile, setImageFile] = useState(null);
  const [videoFile, setVideoFile] = useState(null);
  const [showEmojiPicker, setShowEmojiPicker] = useState(false);
  const [message, setMessage] = useState("");
  const [collapsed, setCollapsed] = useState(false);

  useEffect(() => {
    if (!user) router.push("/login");
    else {
      fetchSuggestions();
      fetchFriendRequests();
    }
  }, [user]);

  if (!user) return null;

  const showTemporaryMessage = (msg) => {
    setMessage(msg);
    setTimeout(() => setMessage(""), 3000);
  };

  const fetchSuggestions = async () => {
    try {
      const res = await fetch("/api/friends/suggestions");
      const data = await res.json();
      setSuggestedUsers(data.users || []);
    } catch (err) { console.error(err); }
  };

  const fetchFriendRequests = async () => {
    try {
      const res = await fetch("/api/friends/requests");
      const data = await res.json();
      setFriendRequests(data.requests || []);
    } catch (err) { console.error(err); }
  };

  const fetchMyFriends = async () => {
    try {
      const res = await fetch("/api/friends/list");
      const data = await res.json();
      setMyFriends(data.friends || []);
    } catch (err) { console.error(err); }
  };

  const handleSendRequest = async (friendId) => { /* ... same as before ... */ };
  const handleAcceptRequest = async (requestId) => { /* ... same as before ... */ };
  const handleRejectRequest = async (requestId) => { /* ... same as before ... */ };
  const handleCreatePost = async (e) => { /* ... same as before ... */ };
  const onEmojiClick = (emojiData) => setContent(prev => prev + emojiData.emoji);

  const styles = {
    page: { display: "flex", minHeight: "100vh", background: theme === "dark" ? "#121212" : "#fff", color: theme === "dark" ? "#f1f1f1" : "#000" },
    main: { flex: 1, padding: "30px" },
    message: { marginTop: "15px", color: "green" },
  };

  return (
    <div style={styles.page}>
      <Sidebar
        collapsed={collapsed}
        setCollapsed={setCollapsed}
        setShowForm={setShowForm}
        handleAddFriendsClick={() => { setShowSuggestions(!showSuggestions); if (!showSuggestions && suggestedUsers.length === 0) fetchSuggestions(); }}
        handleShowFriendRequests={() => { setShowFriendRequests(!showFriendRequests); if (!showFriendRequests) fetchFriendRequests(); }}
        handleShowMyFriends={() => { setShowMyFriends(!showMyFriends); if (!showMyFriends) fetchMyFriends(); }}
        theme={theme}
      />

      <div style={styles.main}>
        <h1>Your Profile</h1>

        <UserInfo user={user} theme={theme} />

        <CreatePostForm
          showForm={showForm}
          title={title} setTitle={setTitle}
          content={content} setContent={setContent}
          imageFile={imageFile} setImageFile={setImageFile}
          videoFile={videoFile} setVideoFile={setVideoFile}
          showEmojiPicker={showEmojiPicker} setShowEmojiPicker={setShowEmojiPicker}
          handleCreatePost={handleCreatePost}
          theme={theme}
          onEmojiClick={onEmojiClick}
        />

        {message && <p style={styles.message}>{message}</p>}

        {showSuggestions && <FriendSuggestions suggestedUsers={suggestedUsers} handleSendRequest={handleSendRequest} theme={theme} />}
        {showFriendRequests && <FriendRequests friendRequests={friendRequests} handleAcceptRequest={handleAcceptRequest} handleRejectRequest={handleRejectRequest} theme={theme} />}
        {showMyFriends && <MyFriends myFriends={myFriends} theme={theme} />}
      </div>
    </div>
  );
}
